body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    height: 80vh;
    width: 100%;
    background-image: url(/Oveluna2.0/static/media/header_bg.15bd4c79.JPG);
    background-size: cover;
    background-repeat: no-repeat;
    display: block;
    overflow: hidden;
    font-family: 'Mali', sans-serif ;
  }

  header nav {
    float: right;
    line-height: 100px;
    
  }

  header nav a {
    display: inline-block;
    color: #fff;
    text-decoration: none;
    padding-top: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    padding-left: 30px;
    line-height: normal;
    -webkit-transition: all 600ms ease;
    transition: all 600ms ease;
    
  }
  
  header nav a:hover {
    background: #d7bde2;
    border-radius: 40px;
    text-decoration:none;
    color: #fff;
  
  }

  .header-content {
    position: absolute;
    z-index: 1;
    top: 10%;
    left: 50%;
    margin-top: 105px;
    margin-left: -145px;
    width: 300px;
    height: 100px;
    text-align: center;
    color: #fff;
  }

  .title {
    /* font-family: "Oswald", sans-serif; */
    font-family: 'Mali', sans-serif ;
    /* font-family: 'Carter One', sans-serif ; */
    font-size: 3.5rem;
  }

.skin {
    width: 100%;
    /* max-height: 100px; */
    overflow: hidden;
    position: absolute;
    margin: auto;
  }


.container-img img {
  display: inline-block;
  width: 70%;
  margin: 10% 1% 0 1%;

}

.header h3 {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  font-size: 1.40em;
  margin: 0;
}
.grid-3 { 
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; /* 30px 30px 30px or repeat(3, 1fr)*/
  grid-gap: 0px;
}

.text-center {
    text-align: center;
    margin: auto;
  }
  
.bg-light {
    background: #fff;
    color: #333;
  }

.services {
    padding: 2rem 0;
  }

.subtitle {
    /* font-family: "Oswald", sans-serif; */
    font-family: 'Mali', sans-serif ;
    /* font-family: 'Carter One', sans-serif ; */
    font-size: 1.5rem;
  }
  
section{
      width: 100%;
      margin: 0;
      color: #fff;
      background: #654ea3; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#eaafc8),
    to(#654ea3)
  );
  background: linear-gradient(
    to right,
    #eaafc8,
    #654ea3
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
      
  }

  section a, p, h3{
    color: #fff;
    font-family: "Open Sans", sans-serif;

  }
  section h3{
    display: block;
    font-size: 1.17em;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    font-weight: bold;
  }


  .contain {
    max-width: 860px;
    margin: auto;
    overflow: hidden;
    /* padding: 5rem 0rem; */
    padding-bottom: 3rem;
    text-align: center;
    
  }

  footer {
    padding: 2.2rem;
    
  }
  
  footer p {
    margin: 0;
    color:  #2f3541;
  }


  .bg-dark {
    background: #2f3541;
    color: #f4f4f4;
  }
  .title {
    font-family: 'Mali', sans-serif ;
    font-size: 3.5rem;
  
  }

